import SharedBrief from 'new-dashboard/components/SharedBrief';
import methods from './shared/methods';
import props from './shared/props';

export default {
  name: 'CondensedMapCard',
  components: {
    MapQuickActions,
    FeaturesDropdown,
    SharedBrief
