<template>
    <div class="dropdown" v-if="isVisible" @click.prevent="copyName">
      <span class="text is-small copy-text" v-if="!copySuccessful"><svg svg-inline="" class="copy-icon" focusable="false" role="presentation" width="16" height="13" viewBox="0 0 16 13" xmlns="http://www.w3.org/2000/svg"><path d="M1.14 0C.51 0 0 .53 0 1.18v10.64C0 12.47.51 13 1.14 13h5.72C7.49 13 8 12.47 8 11.82V10H7v2H1V1h6v2h1V1.18C8 .53 7.49 0 6.86 0H1.14zM10 0v1h5v11h-5v1h5c.55 0 1-.53 1-1.18V1.18C16 .53 15.55 0 15 0h-5zM2 8v1h4V8H2zm0-2v1h4V6H2zm0-2v1h4V4H2zm8 6l4-3.5L10 3v2H7v3h3v2z" fill="#036FE2"/></svg>{{$t(`DatasetCard.copy`)}}</span>
      <span class="text is-small is-txtGrey" v-if="copySuccessful"><svg svg-inline="" class="copy-icon" focusable="false" role="presentation" width="14" height="13" viewBox="0 0 14 13" xmlns="http://www.w3.org/2000/svg"><path d="M6.361.667V2a4.672 4.672 0 0 0-4.666 4.667 4.672 4.672 0 0 0 4.666 4.666 4.672 4.672 0 0 0 4.667-4.666h1.333c0 3.308-2.692 6-6 6s-6-2.692-6-6c0-3.309 2.692-6 6-6zm6.022-.528l1.29 1.056L7.089 9.24 3.772 5.923 4.95 4.744 6.966 6.76 12.383.139z" fill="#2E3C43"/></svg>{{$t(`DatasetCard.copySuccess`)}}</span>
    </div>
</template>

<script>
export default {
  name: 'CopyDropdown',
  props: {
    textToCopy: String,
    isVisible: Boolean
  },
  data () {
    return {
      copySuccessful: false,
      hideDropdownTimeout: null
    };
  },
  watch: {
    isVisible: function (newVal, oldVal) {
      if (newVal === false) {
        this.resetDropdown();
      }
    }
  },
  methods: {
    copyName () {
      var textArea = document.createElement('textarea');
      textArea.style.position = 'fixed';
      textArea.style.opacity = 0;
      textArea.style.height = 0;
      textArea.style.width = 0;
      textArea.value = this.textToCopy;
      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();
      try {
        this.copySuccessful = document.execCommand('copy');
        if (this.copySuccessful) {
          this.hideDropdownTimeout = setTimeout(() => { this.$emit('hideDropdown'); }, 2000);
        }
      } catch (err) {
        this.copySuccessful = false;
      } finally {
        document.body.removeChild(textArea);
      }
    },
    resetDropdown () {
      this.copySuccessful = false;
      if (this.hideDropdownTimeout) {
        clearTimeout(this.hideDropdownTimeout);
      }
    }
  }
};
</script>

