<template>
  <Page>
    <div class="notifications-list-container container grid grid__content">
      <div class="full-width">
        <SectionTitle class="grid-cell" :showActionButton="false" ref="headerContainer">
          <template slot="icon">
            <img src="../assets/icons/section-title/envelope.svg">
          </template>
          <template slot="title">{{ pageTitle }}</template>
        </SectionTitle>
        <ul v-if="!emptyState" class="notifications-list grid-cell  grid-cell--col9 grid-cell--col12--tablet">
          <li class="notification-item" v-for="notification in notifications" :key="notification.id">
            <NotificationCard
              :receivedAt="notification.received_at"
              :readAt="notification.read_at"
              :htmlBody="notification.html_body">
            </NotificationCard>
          </li>
        </ul>
        <EmptyState v-if="!isFetching && emptyState" :text="emptyStateText">
          <svg svg-inline="" focusable="false" role="presentation" width="60" height="60" xmlns="http://www.w3.org/2000/svg"><g fill="none" fill-rule="evenodd" opacity=".9"><path d="M-2-2h64v64H-2z"/><g fill="#E6E8EB"><path d="M30.112 59.333c-3.981 0-7.712-1.97-10.01-5.157-3.88.637-7.929-.632-10.731-3.432-2.816-2.816-4.059-6.848-3.43-10.73C2.75 37.716.78 33.983.78 30c0-3.984 1.97-7.715 5.162-10.01-.629-3.883.614-7.915 3.427-10.731 2.803-2.803 6.845-4.07 10.733-3.43C22.397 2.637 26.128.667 30.112.667s7.712 1.97 10.01 5.162c3.894-.64 7.929.63 10.731 3.43 2.816 2.818 4.059 6.85 3.43 10.733 3.192 2.296 5.162 6.027 5.162 10.01 0 3.982-1.97 7.718-5.162 10.014.632 3.88-.614 7.912-3.427 10.728a12.233 12.233 0 0 1-8.704 3.597c-.683 0-1.36-.056-2.03-.162-2.298 3.186-6.029 5.154-10.01 5.154zM22.747 47.78l1.032 2.178C24.96 52.453 27.387 54 30.112 54s5.15-1.55 6.33-4.04l1.035-2.181 2.272.81c2.531.902 5.443.272 7.336-1.616 1.923-1.928 2.544-4.738 1.616-7.333l-.81-2.272 2.178-1.035c2.494-1.181 4.043-3.61 4.043-6.333 0-2.723-1.55-5.15-4.04-6.328l-2.181-1.032.81-2.275c.928-2.597.307-5.41-1.618-7.336-1.888-1.888-4.803-2.52-7.334-1.618l-2.272.81-1.034-2.178C35.26 7.552 32.835 6 30.112 6c-2.723 0-5.152 1.552-6.333 4.043l-1.032 2.178-2.272-.81c-2.534-.902-5.446-.27-7.336 1.618-1.923 1.926-2.544 4.742-1.619 7.339l.81 2.275-2.18 1.029C7.658 24.853 6.111 27.277 6.111 30c0 2.723 1.55 5.152 4.043 6.333l2.178 1.035-.813 2.272c-.925 2.595-.307 5.405 1.619 7.333 1.89 1.888 4.805 2.52 7.333 1.616l2.275-.81z" fill-rule="nonzero"/><path d="M28.28 42.565l-10.685-7.122 3.698-5.55 5.315 3.547 11.456-16.043 5.427 3.875z"/></g></g></svg>
        </EmptyState>
        <LoadingState v-if="isFetching" :text="loadingStateText" class="loading-state"></LoadingState>
      </div>
    </div>
  </Page>
</template>

<script>
import Page from 'new-dashboard/components/Page';
import EmptyState from '../components/States/EmptyState';
import LoadingState from '../components/States/LoadingState';
import NotificationCard from '../components/NotificationCard';
import SectionTitle from '../components/SectionTitle';

export default {
  name: 'NotificationsPage',
  components: {
    Page,
    EmptyState,
    LoadingState,
    NotificationCard,
    SectionTitle
  },
  computed: {
    pageTitle () {
      return this.$t(`NotificationsPage.header.title`);
    },
    notifications () {
      return this.$store.state.notifications.notifications;
    },
    emptyState () {
      return !this.notifications || !this.notifications.length;
    },
    emptyStateText () {
      return this.$t(`NotificationsPage.emptyState`);
    },
    loadingStateText () {
      return this.$t(`NotificationsPage.loadingState`);
    },
    isFetching () {
      return this.$store.state.notifications.isFetching;
    }
  },
  mounted: function () {
    this.$store.dispatch('user/resetOrganizationNotifications');
  }
};
</script>

