<template>
  <Modal ref="deleteAppModal" name="deleteAppModal">
    <div class="oauthapps__modal">
      <div class="oauthapps__modal-inner">
        <div class="oauthapps__icon u-mb--24">
          <svg class="oauthapps_svgicon" svg-inline="" focusable="false" role="presentation" width="39" height="35" xmlns="http://www.w3.org/2000/svg"><g fill="none" fill-rule="evenodd"><path d="M-1-3h41.03v40.7H-1z"/><g fill="#E7E9EC" fill-rule="nonzero"><path d="M34.901.392H4.129c-1.886 0-3.42 1.52-3.42 3.391v27.134c0 1.87 1.534 3.391 3.42 3.391H34.9c1.886 0 3.42-1.52 3.42-3.391V3.783c0-1.87-1.534-3.391-3.42-3.391zm0 6.783H21.224V3.783h13.677v3.392zm-22.224 0V3.783h5.128v3.392h-5.128zm-3.42-3.392v3.392H4.13V3.783h5.128zM4.13 30.917v-20.35H34.9l-.003 20.35H4.129z"/><path d="M21.224 22.438h8.548v3.391h-8.548zM9.758 16.855l3.92 3.887-3.92 3.89 2.418 2.398 6.337-6.288-6.337-6.285z"/></g></g></svg>
          <svg class="oauthapps__badge" svg-inline="" focusable="false" role="presentation" width="18" height="19" xmlns="http://www.w3.org/2000/svg"><g fill="none" fill-rule="evenodd"><path fill="#FFF" d="M3 6h12v12H3z"/><path d="M2 17c0 1.103.897 2 2 2h10c1.103 0 2-.897 2-2V5H2v12zM4 7h10l.001 10H4V7zm8-5V0H6v2H0v2h18V2z" fill="#EB1510" fill-rule="nonzero"/><path fill="#EB1510" fill-rule="nonzero" d="M6 9h2v6H6zm4 0h2v6h-2z"/></g></svg>
        </div>
        <p class="text is-caption u-mb--8" v-html="$t('OAuthAppsPage.deleteModal.title', { name: app.name })"></p>
        <p class="text is-small is-txtSoftGrey" v-html="$t('OAuthAppsPage.deleteModal.subtitle')"></p>
        <div class="oauthapps__modal-actions">
          <button class="oauthapps__button button button--ghost u-mr--12" @click="close">{{ $t(`OAuthAppsPage.deleteModal.cancelButton`) }}</button>
          <button class="oauthapps__modal-button oauthapps__modal-button--delete" @click="deleteApp">{{ $t(`OAuthAppsPage.deleteModal.deleteButton`) }}</button>
        </div>
      </div>
    </div>
  </Modal>
</template>

<script>
import Modal from 'new-dashboard/components/Modal';

export default {
  name: 'DeleteAppModal',
  components: {
    Modal
  },
  props: {
    app: {
      type: Object,
      required: true
    }
  },
  methods: {
    open () {
      this.$refs.deleteAppModal.open();
    },
    close () {
      this.$refs.deleteAppModal.close();
    },
    deleteApp () {
      this.$store.dispatch('oAuthApps/delete', this.app)
        .then(() => {
          this.$router.push({ name: 'oauth_apps_list' });
        });
    }
  }
};
</script>

<style scoped lang="scss">
@import 'new-dashboard/styles/variables';
